import React from "react";
import { Cell } from "../../state";
import CodeCell from "../code-cell/code-cell";
import ActionBar from "../action-bar/action-bar";
import TextEditor from "../text-editor/text-editor.component";

interface CellListItemProps {
  cell: Cell;
}

const styles: { [index: string]: React.CSSProperties } = {
  "action-bar-wrapper": {
    width: "100%",
    height: "30px",
    backgroundColor: "#37414b",
  },
  "action-bar": {
    zIndex: 10,
    position: "relative",
  },
};

const CellListItemComponent = ({ cell }: CellListItemProps) => {
  let child: JSX.Element;

  if (cell.type === "code") {
    child = (
      <>
        <div style={styles["action-bar-wrapper"]}>
          <ActionBar id={cell.id} />
        </div>
        <CodeCell cell={cell} />
      </>
    );
  } else {
    child = (
      <>
        <TextEditor cell={cell} />
        <ActionBar id={cell.id} />
      </>
    );
  }

  return <div style={styles["action-bar"]}>{child}</div>;
};

export default CellListItemComponent;
