import "./text-editor.styles.css";
import { Cell } from "../../state";
import MDEditor from "@uiw/react-md-editor";
import { useActions } from "../../hooks/use-actions";
import React, { useState, useEffect, useRef } from "react";

interface TextEditorProps {
  cell: Cell;
}

const TextEditor = ({ cell }: TextEditorProps) => {
  const ref = useRef<HTMLDivElement | null>(null);
  const [editing, setEditing] = useState(false);

  const { updateCell } = useActions();

  const updateContent = (value?: string) => {
    updateCell(cell.id, value as string);
  };

  useEffect(() => {
    const listener = (event: MouseEvent) => {
      if (
        ref.current &&
        event.target &&
        ref.current.contains(event.target as Node)
      ) {
        return;
      }

      setEditing(false);
    };

    document.addEventListener("click", listener, { capture: true });

    return () => {
      document.removeEventListener("click", listener, { capture: true });
    };
  }, []);

  if (editing) {
    return (
      <div ref={ref} className="text-editor">
        <MDEditor value={cell.content} onChange={updateContent} />
      </div>
    );
  }

  const setEditingTrue = () => {
    setEditing(true);
  };

  return (
    <div className="text-editor card" onClick={setEditingTrue}>
      <div className="class-content">
        <MDEditor.Markdown
          source={cell.content || "Click to Edit"}
          style={{ whiteSpace: "pre-wrap" }}
        />
      </div>
    </div>
  );
};

export default TextEditor;
