import { Cell } from "../../state";
import { useEffect, useState } from "react";
import Resizable from "../resizable/resizable";
import Preview from "../preview/preview.component";
import esBundler from "../bundler/bundler.component";
import { useActions } from "../../hooks/use-actions";
import CodeEditor from "../code-editor/code-editor.component";

interface CodeCellProps {
  cell: Cell;
}

const CodeCell = ({ cell }: CodeCellProps) => {
  const [code, setCode] = useState("");
  const [err, setErr] = useState("");
  const { updateCell } = useActions();

  const updateContent = (value: string) => {
    updateCell(cell.id, value);
  };

  useEffect(() => {
    let timer = setTimeout(async () => {
      const { code, err } = await esBundler(cell.content);
      setCode(code);
      setErr(err);
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [cell.content]);

  return (
    <Resizable direction="vertical">
      <div style={{ height: "100%", display: "flex", flexDirection: "row" }}>
        <Resizable direction="horiziontal">
          <CodeEditor initialValue={cell.content} onChange={updateContent} />
        </Resizable>
        <Preview code={code} err={err} />
      </div>
    </Resizable>
  );
};

export default CodeCell;
