import * as esbuild from "esbuild-wasm";
import axios from "axios";
import localForage from "localforage";

const fileCache = localForage.createInstance({
  name: "livebookCache",
});

export const fetchPlugin = (input: string) => {
  return {
    name: "fetch-plugin",
    setup: function (build: esbuild.PluginBuild) {
      build.onLoad({ filter: /(^index\.js$)/ }, async (args: any) => {
        return {
          loader: "jsx",
          contents: `
            ${input}
          `,
        };
      });

      build.onLoad({ filter: /.*/ }, async (args: any) => {
        const npmPackage = await fileCache.getItem<esbuild.OnLoadResult>(
          args.path
        );

        if (npmPackage) return npmPackage;
        return null;
      });

      build.onLoad({ filter: /.css$/ }, async (args: any) => {
        const { data, request } = await axios.get(args.path);

        const escaped = data
          .replace(/\n/g, "")
          .replace(/"/g, '\\"')
          .replace(/'/g, "\\'");

        const contents = `
            const style = document.createElement('style');
            style.innerText = '${escaped}';
            document.head.appendChild('style');
        `;

        const returnedPackage: esbuild.OnLoadResult = {
          loader: "jsx",
          contents,
          resolveDir: new URL("./", request.responseURL).pathname,
        };

        await fileCache.setItem(args.path, returnedPackage);

        return returnedPackage;
      });

      build.onLoad({ filter: /.*/ }, async (args: any) => {
        const { data, request } = await axios.get(args.path);

        const returnedPackage: esbuild.OnLoadResult = {
          loader: "jsx",
          contents: data,
          resolveDir: new URL("./", request.responseURL).pathname,
        };

        await fileCache.setItem(args.path, returnedPackage);

        return returnedPackage;
      });
    },
  };
};
