import { ActionTypes } from "../action-types.ts";
import { Action } from "../actions";
import { Cell } from "../cell";
import produce from "immer";

interface CellsState {
  loading: boolean;
  error: string | null;
  order: string[];
  data: {
    [key: string]: Cell;
  };
}

const initialState: CellsState = {
  loading: false,
  error: null,
  order: [],
  data: {},
};

const reducer: (base: CellsState | undefined, action: Action) => CellsState =
  produce((state: CellsState = initialState, action: Action) => {
    switch (action.type) {
      case ActionTypes.MOVE_CELL:
        const { id: idm, direction } = action.payload;
        const idx = state.order.findIndex((value) => value === idm);
        const targetIndex = direction === "up" ? idx - 1 : idx + 1;

        if (targetIndex < 0 || targetIndex > state.order.length) {
          return state;
        }

        state.order[idx] = state.order[targetIndex];
        state.order[targetIndex] = action.payload.id;
        return state;
      case ActionTypes.DELETE_CELL:
        delete state.data[action.payload];
        const index = state.order.findIndex(
          (value) => value === action.payload
        );
        if (index !== -1) state.order.splice(index, 1);
        return state;
      case ActionTypes.UPDATE_CELL:
        const { id, content } = action.payload;
        state.data[id].content = content;
        return state;
      case ActionTypes.INSERT_CELL_BEFORE:
        const { id: idy, type } = action.payload;

        const cell: Cell = {
          id: randomId(),
          content: "",
          type,
        };

        if (!idy) {
          state.data[cell.id] = cell;
        }

        const idz = state.order.findIndex((value) => value === idy);

        if (idz < 0) {
          state.order.push(cell.id);
        } else {
          state.order.splice(idz, 0, cell.id);
        }

        return state;
      default:
        return state;
    }
  });

const randomId = () => {
  return Math.random().toString(36).substr(2, 5);
};

export default reducer;
